import React from 'react'
import firstTaskImage from 'images/firstTask.png'
import 'stylesheets/TimerAppStyles/CreateFirstTodo.css'

const CreateFirstTodo = () => {
  return (
    <div className='firstTodo-container'>
      <img src={firstTaskImage} alt='Create your first todo!' className='firstTodo-img'/>
      <p>Create your first TO-DO!</p>
    </div>
  )
}

export {CreateFirstTodo}